import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { NewExternalSystemComponent } from './new-external-system/new-external-system.component';

const routes: Routes = [
  {
    path:"",
    component:NewExternalSystemComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class NewExternalSystemRoutingModule { }
